import React, { useEffect } from "react";
import { Checkbox,CircularProgress } from "@material-ui/core";
import { useDispatch, useSelector } from "react-redux";
import { Check } from "../../redux/action";
import {fetchData } from "../../redux/";
import Moment from "moment";
import CustomModal from "./CustomModal";
import { DeletePopup } from "./DeletePopup";

function Table(props) {
  const lg = "col-lg-" + props.lg;
  const data = props.Data ?  props.Data : null ;
  const dispatch = useDispatch();
  const currentDate = props.currentDate ? true : false;
  const [modalShow, setModalShow] = React.useState(false);
  const [deletePopup, setDeletePopup] = React.useState(false);
  const [itemdata, setData] = React.useState(null);
  const user = useSelector((state) => state.User.user);
  const id = { id: user ? user.googleId : null }
  let isLoading = useSelector((state)=>state.Task.loading)
  useEffect(() => {
    dispatch(fetchData("task/",id));
  }, []);
  return (
    <div className="row justify-content-lg-center col-lg-12">
      <CustomModal show={modalShow} onHide={() => setModalShow(false)} />
      <DeletePopup show={deletePopup} data={itemdata} onHide={()=> setDeletePopup(false)} />

      <div className={lg}>
        <div className="card">
          <div className="card-header card-header-tabs card-header-primary">
            <div className="nav-tabs-navigation">
              <div className="nav-tabs-wrapper">
                <ul className="nav nav-tabs" data-tabs="tabs">
                <li className="nav-item">
                    <button
                      className="nav-link btn btn-link"
                      onClick={() => setModalShow(true)}
                      data-toggle="tab"
                    >
                      <i className="material-icons">add</i> Add
                      <div className="ripple-container" />
                    </button>
                  </li>
                </ul>
              </div>
            </div>
          </div>

          <div className="card-body">
            <div className="tab-content">
              <div className="tab-pane active" id="profile">
                <table className="table">
                  <tbody>
                  { isLoading ? <tr>
                        <td colSpan="5" style={{ textAlign: "center" }}>
                          <CircularProgress />
                        </td>
                      </tr> :
                     
                     data ? (
                      data.map((item) => {
                        if (
                          currentDate
                            ? Moment(item.data.Date).format("DD-MM-YYYY") ===
                              Moment().format("DD-MM-YYYY")
                            : true
                        ) {
                          const TaskDate = Moment(item.data.Date).format(
                            "DD-MM-YYYY"
                          );
                          const TaskTime = Moment(item.data.Date).format(
                            "hh:mm A"
                          );
                          return (
                            <tr key={item.id}>
                              <td>
                                <Checkbox
                                  color="primary"
                                  checked={item.status}
                                  onChange={() => dispatch(Check())}
                                />
                              </td>
                              <td> {item.data.Task} </td>
                              <td> {TaskDate} </td>
                              <td> {TaskTime} </td>
                              <td className="td-actions text-right">
                                <button
                                  type="button"
                                  rel="tooltip"
                                  title="Edit Task"
                                  className="btn btn-primary btn-link btn-sm"
                                  onClick={() => {
                                    alert("EDIT", item.id);
                                    console.log(item);
                                  }}
                                >
                                  <i className="material-icons">edit</i>
                                </button>
                                <button
                                  type="button"
                                  rel="tooltip"
                                  title="Remove"
                                  className="btn btn-danger btn-link btn-sm"
                                  onClick={()=>{setDeletePopup(true);setData(item)}}
                                >
                                  <i className="material-icons">close</i>
                                </button>
                              </td>
                            </tr>
                          );
                        }
                      })
                    ) : 
                    (<tr>
                    <td colSpan="5" style={{ textAlign: "center" }}>
                      <h3>Start Your Day</h3>
                    </td>
                    </tr>)                      
                      }
                     
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Table;
